<template>
  <div class="chat-room-view" v-if="userName">
    <div>
      <button
        class="btn btn-primary del-btn"
        @click.stop.prevent="deleteMessage"
      >
        <i class="fa-solid fa-trash"></i>
      </button>
    </div>
    <div class="container messages-container pt-3">
      <div
        class="d-flex justify-content-center"
        v-if="page !== 1 && page !== 0"
      >
        <button
          class="btn btn-primary button-color"
          @click.stop.prevent="getNextPage"
        >
          更多訊息...
        </button>
      </div>
      <div
        v-for="(message, index) in messages"
        :key="message.id"
        :id="isLastMsg(index)"
      >
        <div class="message-style">
          <div class="message-send-user" v-if="isSameUser(index)">
            {{ message.sendUser | getAlpha }}
          </div>
          <div v-else class="message-send-user-fake"></div>
          <div class="message-send-message" v-if="message.type === 'text'">
            <div class="send-message" v-linkified>{{ message.message }}</div>
            <div class="send-time">
              {{ message.sendTime | sendTime }}
            </div>
          </div>
          <div
            class="message-send-message"
            v-else-if="message.type === 'image'"
          >
            <div class="send-message">
              <a :href="message.message">
                <img :src="message.message" alt="" class="img-message" />
              </a>
            </div>
            <div class="send-time">
              {{ message.sendTime | sendTime }}
            </div>
          </div>
        </div>
      </div>
    </div>
    <img v-if="img" :src="img" class="d-block mb-3 upload-img" width="200" />
    <div class="input-container">
      <input
        type="text"
        class="input-area mr-3 ml-3"
        v-model="inputText"
        @keyup.enter="sendMessage"
      />
      <button
        class="btn btn-primary button-color mr-2"
        @click.stop.prevent="sendMessage"
      >
        <i class="fa-regular fa-paper-plane"></i>
      </button>
      <button
        class="btn btn-primary button-color mr-3"
        @click="$refs.file.click()"
      >
        +
      </button>
      <input
        ref="file"
        type="file"
        name="img"
        accept="image/*"
        class="d-none"
        @change="handleFileChange"
      />
    </div>
  </div>
  <div class="main-view" v-else>
    <div class="main-input">
      <input
        type="text"
        class="mr-3"
        placeholder="input your name..."
        v-model="userNameInput"
        @keyup.enter="enterChat"
      />
      <button class="btn btn-primary" @click="enterChat">ENTER</button>
    </div>
  </div>
</template>
<script>
import moment from "moment";
import { io } from "socket.io-client";
import { v4 as uuidv4 } from "uuid";
import messageAPI from "../../apis/messages";

export default {
  data() {
    return {
      name: "",
      img: "",
      userNameInput: "",
      userName: "",
      inputText: "",
      messages: [],
      socketInstance: null,
      page: 1,
      file: "",
    };
  },
  created() {
    const { name } = this.$route.params;
    this.initRoom(name);
    this.fetchMessages();
    this.connect();
  },
  // updated() {
  //   console.log('update')
  //   this.scrollToLast();
  // },
  beforeRouteUpdate(to, from, next) {
    const { name } = to.params;
    this.initRoom(name);
    this.fetchMessages();
    next();
  },
  beforeDestroy() {
    this.disconnect();
    localStorage.removeItem("UserName");
  },
  watch: {
    userName(newValue) {
      this.userName = newValue;
    },
  },
  methods: {
    fetchMessages(messages) {
      this.messages = messages || [];
    },
    isSameUser(index) {
      return (
        (index > 0 &&
          this.messages[index].sendUser !==
            this.messages[index - 1].sendUser) ||
        index === 0
      );
    },
    isLastMsg(index) {
      if (this.messages.length - 1 === index) {
        return "lastMsg";
      } else {
        return false;
      }
    },
    initRoom(name) {
      this.name = name;
      this.userName = localStorage.getItem("UserName")
        ? localStorage.getItem("UserName")
        : "";
    },
    enterChat() {
      if (!this.userNameInput) {
        return;
      }
      localStorage.setItem("UserName", this.userNameInput);
      this.userName = this.userNameInput;
    },
    connect() {
      const url = "https://protected-ocean-08526.herokuapp.com/";
      // const url = "http://localhost:3000";
      this.socketInstance = io(url);

      this.socketInstance.emit("join", {
        roomName: this.name,
        sendUser: this.userName,
      });

      this.socketInstance.on("person joined", (data) => {
        // console.log(data)

        this.fetchMessages(data.historyMessages.rows);
        this.page = data.pagination.page;
      });

      this.socketInstance.on("message:recieved", async (data) => {
        await this.messages.push(data);
        this.scrollToLast();
      });
    },
    scrollToLast() {
      const el = document.querySelector("#lastMsg");
      if (el) {
        el.scrollIntoView({
          block: "start",
          behavior: "smooth",
        });
      }
    },
    disconnect() {
      this.socketInstance.disconnect();
    },
    async sendMessage() {
      if (this.inputText) {
        const data = {
          id: uuidv4(),
          roomName: this.name,
          sendUser: this.userName,
          message: this.inputText,
          type: "text",
          sendTime: new Date(),
        };

        this.socketInstance.emit("message", data);
        this.inputText = "";
      } else if (this.file) {
        const formData = new FormData();
        formData.append("userName", this.userName);
        formData.append("roomName", this.name);
        formData.append("img", this.file);

        const { data } = await messageAPI.uploadImg({ formData });

        const imgData = {
          id: uuidv4(),
          roomName: this.name,
          sendUser: this.userName,
          message: data.data,
          type: "image",
          sendTime: new Date(),
        };

        this.socketInstance.emit("message", imgData);
        this.img = "";
        this.file = "";
      }
    },
    // uploadImg(){

    // },
    handleFileChange(e) {
      const { files } = e.target;

      if (files.length === 0) {
        // 使用者沒有選擇上傳的檔案
        this.img = "";
      } else {
        // 否則產生預覽圖
        const imageURL = window.URL.createObjectURL(files[0]);
        this.img = imageURL;
        this.file = files[0];
      }
    },
    async deleteMessage() {
      const result = await messageAPI.deleteMessages();
      this.messages = [];
      console.log(result);
    },
    async getNextPage() {
      this.page = this.page - 1;
      const { data } = await messageAPI.getNextPage({
        page: this.page,
        roomName: this.name,
      });
      this.messages.unshift(...data.data.nextPageMessages);
    },
  },
  filters: {
    getAlpha(name) {
      return name?.substring(0, 1);
    },
    fromNow(datetime) {
      if (!datetime) return "-";
      const now = new Date();
      if (datetime.getDate() + 1 < now.getDate()) {
        return moment(datetime).formate("YYYYMMDD HH:mm");
      } else {
        return moment(datetime).fromNow();
      }
    },
    sendTime(datetime) {
      if (!datetime) return "-";
      return moment(datetime).format("HH:mm");
    },
  },
};
</script>
<style>
.chat-room-view {
  height: 100%;
  /* display: flex; */
  /* justify-content: center; */
}
.messages-container {
  height: calc(100vh - 108px);
  /* padding-bottom: 10%; */
  overflow-y: auto;
}

.input-container {
  width: 100%;
  display: flex;
  margin-top: 12px;
  position: relative;
  /* position: fixed; */
  /* bottom: 10px; */
}
.input-area {
  height: 32px;
  width: 100%;
  resize: none;
  outline: none;
  border: 0;
  background: rgb(40, 39, 39);
  border-radius: 50px;
  padding: 5px 15px;
  font-size: 16px;
  line-height: 18px;
  color: #828688;
}
.message-style {
  display: flex;
  align-items: center;
  margin: 10px 0;
}
.message-send-user {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #8c9093;
  color: rgb(195, 195, 195);
  line-height: 30px;
  text-align: center;
}
.message-send-user-fake {
  width: 30px;
  background: #191718;
}
.button-color {
  color: rgb(236, 235, 235);
  background: #497ebf;
}
.message-send-message {
  width: 100%;
  border-radius: 3px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-left: 10px;
  padding-left: 5px;
  background: rgb(40, 39, 39);
  color: #828688;
  display: flex;
}
.send-message {
  flex: 4;
}
.send-time {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  align-self: end;
  padding-left: 10px;
  padding-right: 5px;
}
.del-btn {
  position: fixed;
  top: 7px;
  right: 15px;
  background: rgb(158, 157, 157);
}
.upload-img {
  position: absolute;
  bottom: 40px;
  left: 20px;
}
.img-message {
  width: 80%;
}

@media screen and (min-width: 761px) {
  .input-container {
    margin: 0 auto;
    width: 60%;
  }
  .img-message {
    width: 20%;
  }
}
</style>